// archivo de store de redux: importacin de los user y productSlice. Importacin de la Api

import { configureStore } from "@reduxjs/toolkit";
import productSlice from "./features/productSlice";
import userSlice from "./features/userSlice";
import appApi from "./services/appApi";

//importacin guardado en store: Si salimos de la pgina no tenemos que loguearnos de nuevo, se guarda todo en el store. 
import storage from "redux-persist/lib/storage";
import { combineReducers } from "redux";
import { persistReducer } from "redux-persist";
import thunk from "redux-thunk";

//reducers: combinacin de los reductores para tener varios slice de productos en el mismo objeto: 
const reducer = combineReducers({
    user: userSlice,
    products: productSlice,
    [appApi.reducerPath]: appApi.reducer,
});

const persistConfig = {
    key: "root",
    storage,
    blackList: [appApi.reducerPath, "products"],
};

// reductores que persisten en el store:
const persistedReducer = persistReducer(persistConfig, reducer);

// creacin del store:


const store = configureStore({
    reducer: persistedReducer,
    middleware: [thunk, appApi.middleware],
});
export default store;
